@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Montserrat|Oswald");
/*colors*/
/*dimensions*/
/*text*/
/*icon*/
.padding {
  padding: 1rem;
}

.mediumPadding {
  padding: 1.5rem;
}

.bigPadding {
  padding: 2rem;
}

.margin {
  margin: 1rem;
}

.mediumMargin {
  margin: 1.5rem;
}

.bigMargin {
  margin: 2rem;
}

.bigMarginHeight {
  margin: 2rem 0 2rem 0;
}

.container {
  width: 70%;
  max-width: none;
}

.mainFixed {
  position: fixed;
  height: 86vh;
  top: 0px;
  left: 15%;
  right: 15%;
  bottom: 14vh;
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mainFixed-zone {
  margin-top: 2rem;
}

.main .footer {
  color: #000;
}

.sidebar {
  background-color: #60487F;
  position: fixed;
  top: 0px;
  width: 15%;
  height: 88vh;
  z-index: 0.5;
}

.sidebar-nav, .sidebar form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sidebar-nav p, .sidebar form p {
  color: white;
}

.sidebar-nav h1, .sidebar-nav h2, .sidebar-nav h3, .sidebar form h1, .sidebar form h2, .sidebar form h3 {
  color: #4AF8FF;
}

.sidebar ul {
  margin-top: 1rem;
}

.sidebar li {
  margin-top: 1rem;
}

.sidebar li p, .sidebar li a {
  display: inline;
}

.sidebar-right {
  right: 0px;
}

.header, .navbar {
  background-color: #414141;
  z-index: 1;
}

.header a, .navbar a {
  color: white;
}

.header a:hover, .navbar a:hover {
  color: #4AF8FF;
  text-decoration: none;
}

.footerFix {
  z-index: 1;
  background-color: #68C2E5;
  height: 14vh;
}

.footerFix .col-12 {
  padding: 0px;
}

/*feed*/
.feed .playlists {
  display: flex;
  flex-direction: row;
}

.feed .playlists .card {
  position: relative;
  display: inline-block;
  margin: 1rem 0 1rem 0;
  flex-grow: 1;
  max-width: 50%;
  width: calc(100% * (1/3) - 2rem);
}

.feed .playlists .card:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.feed .playlists .card div:first-child {
  position: absolute;
  width: 100%;
  height: 100%;
}

.feed .sounds {
  display: flex;
  flex-direction: row;
}

.feed .sounds .card {
  display: inline-block;
  margin: 0 1rem 0 1rem;
  flex-grow: 1;
  max-width: 50%;
  width: calc(100% * (1/3) - 2rem);
  /*faire des carrés parfait responsive*/
  overflow-y: hidden;
}

.feed .sounds .card:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.feed .sounds .card div:first-child {
  position: absolute;
  width: 100%;
  height: 100%;
}

.feed .sounds .card:hover .card-body {
  top: 0px;
  margin-top: 0px;
  border-radius: 0px;
  background-color: rgba(74, 249, 255, 0.733);
}

.feed .sounds .card .card-body {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 100%;
  transition: all ease-in-out 0.5s;
  overflow-x: hidden;
}

.feed .sounds .card .card-body h1, .feed .sounds .card .card-body h2, .feed .sounds .card .card-body h3, .feed .sounds .card .card-body h4, .feed .sounds .card .card-body h5, .feed .sounds .card .card-body h6 {
  text-align: center;
}

.feed .sounds .card .card-buttons {
  margin: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.feed .sounds .card .card-buttons a {
  margin: 1rem;
}

.feed .sounds .card .dropdown {
  text-align: center;
}

.feed .sounds .card a, .feed .sounds .card p, .feed .sounds .card h1, .feed .sounds .card h2, .feed .sounds .card h3, .feed .sounds .card h4, .feed .sounds .card h5, .feed .sounds .card h6 {
  color: #3D013C;
}

.feed .endFeed {
  height: 6vh;
}

.userProfile h1 {
  text-align: center;
}

.nouvelle .form-control {
  width: 50%;
}

div,
p,
a,
li {
  /*Mettre du Montserrat à la place*/
  font-family: "Montserrat", Helvetica, sans-serif;
}

.btn {
  font-size: 1rem;
}

.btn span {
  font-size: 0.8rem;
}

h1,
h2,
h3 {
  font-family: "Oswald", sans-serif;
  color: #60487F;
}

.h1 {
  font-size: 3rem;
}

.h2 {
  font-size: 2rem;
}

.h3 {
  font-size: 1.5rem;
}

p {
  color: black;
  opacity: 80%;
  font-size: 1rem;
}

.p1 {
  font-size: 1.5rem;
}

.p2 {
  font-size: 1.2rem;
}

.p3 {
  font-size: 1rem;
}

i {
  display: inline;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

a {
  text-decoration: none;
  color: #7888BF;
}

a:hover, a:link, a:active {
  color: #68C2E5;
  text-decoration: none;
}

ul {
  display: inline-block;
  list-style-type: none;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
}

li {
  list-style: none;
}

/*font style*/
.uppercase {
  text-transform: uppercase;
}

.light {
  font-weight: lighter;
}

.bold {
  font-weight: bold;
}

/*color*/
.pDark {
  color: #0e0e0e;
}

.pDarkGrey {
  color: #414141;
}

.pWhite {
  color: white;
}

.pWhite:hover {
  color: white;
}

.pGrey {
  color: #9b9b9b;
}

.pGrey:hover {
  color: #9b9b9b;
}

.littleIcon {
  font-size: 1rem;
}

.bigIcon {
  font-size: 1.5rem;
}

.fatIcon {
  font-size: 2rem;
}

.form-control {
  border-radius: 20px;
  box-shadow: #60487F;
}

.form-control:focus {
  border-color: #60487F;
  box-shadow: 0 0 0 0.2rem rgba(96, 72, 127, 0.13);
}

.btn {
  border-radius: 20px;
  transition: all .2s ease-in-out;
}

.btn:focus {
  border-color: #60487F;
  box-shadow: 0 0 0 0.2rem rgba(96, 72, 127, 0.13);
}

.btnPurple {
  color: #60487F !important;
  border-color: #60487F;
}

.btnPurple:hover {
  color: white !important;
  background-color: #60487F;
}

.btnPurpleDark {
  color: #3D013C !important;
  border-color: #3D013C;
}

.btnPurpleDark:hover {
  color: white !important;
  background-color: #3D013C;
}

.btnCyan {
  color: #4AF8FF !important;
  border-color: #4AF8FF;
}

.btnCyan :hover {
  color: white !important;
  background-color: #4AF8FF;
}

.card {
  border-radius: 10px;
}

.card-img-top {
  border-radius: 10px;
}

/* player son : les css */
.wrapper-son {
  position: relative;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 6px;
  background-size: cover;
  background-position: center center;
  box-sizing: border-box;
}

.son-info {
  font-size: 1rem;
  padding: 6px;
  color: #666;
  display: flex;
  flex-wrap: wrap;
}

.son-info .played, .son-info .duration {
  width: 50%;
}

.son-info .titre {
  width: 100%;
  padding-top: 4px;
  text-align: center;
  font-weight: bold;
}

.son-info .auteur {
  font-weight: normal;
  display: block;
  padding-top: 4px;
}

.son-info .duration {
  text-align: right;
}

.timeline {
  box-sizing: border-box;
  background-color: #68C2E5;
  cursor: col-resize;
}

.bar-played {
  height: 100%;
  width: 0;
  background-color: #4AF8FF;
  height: 10px;
}
